ul {
  list-style: none;
}
.media {
  padding: 1rem;
}
.social-buttons {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color:rgb(51, 34, 18, 65%);
  color: #fff;
  box-shadow: .5rem 1rem 1rem .5rem rgba(0,0,0,.15)!important;
}

.feature-item, .bb-item:hover img, .recent-review-content img {
  transition: all .2s;
}

.feature-item:hover, .bb-item:hover img, .recent-review-content:hover img{
  transform: translateY(-10px);
  box-shadow: 0 1rem 1rem rgb(255, 255, 255,.15)!important;
}

.attraction-section {
  /* background-image: url('../images/home/together.jpg');
  background-size: cover; */
  width: 80%;
}

.slide-container {
  scroll-snap-type: x mandatory;
  overflow-x: hidden;
  display: flex;
  padding: 0rem;
  border-radius: .25rem!important;
  width: 80%;
  min-width: 300px;
}
.slide {
  scroll-snap-align: start;
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 3s ease-in-out;
}

.team-header-content{
  position: relative;
  top: 25%;
  text-align: left;
}

.team-header-content p {
  padding: 1rem;
  width: 85%;
}

.team-name {
  transition: .5s ease;
}

.team-christian {
  background-image: url('../images/team/christian.png');
  background-size: cover;
  bottom: 300px;
  right: 35%;
}

.team-shane {
  background-image: url('../images/team/Shane.png');
  background-size: cover;
}

.team-member{
  background-size: cover;
  transition: all .3s;
  width: 60%;
  height: 500px;
  margin: auto;
  min-width: 270px;
  display: flex;
  align-items: center;
  position: relative;
  float: right;
}

.team-member:hover .social-buttons {
  opacity: 1;
}

.team-member:hover {
  transform: translateY(-10px);
  box-shadow: 0 1rem 1rem rgba(0,0,0,.15)!important;
  z-index: 100;
}

.recent-review-content {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: auto;
}

.team-member:hover .team-name {
  opacity: 0;
}

.contact-section {
  background-image: linear-gradient(105deg, rgba(51, 34, 18, 0.9) 0%, rgba(51, 34, 18, 0.9) 52%, transparent 52%), url('/media/contact-background.jpg');
background-size: 100%;
height: 400px;
background-size: auto;
}

.bb-img-home {
  transition: all .2s;
}
.bb-img-home:hover {
  transform: translateY(-10px);
  box-shadow: 0 1rem 1rem rgba(0,0,0,.15)!important
}

.view-more:link,
.view-more:visited {
  color: rgb(234, 191, 0);
  transition: all .2s;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: .25rem;
  line-height: 1.5;
  display: inline-block;
  padding: .375rem .75rem;
}

.view-more:link::after,
.view-more:link::after {
  content: "...";
  margin-left: 1px;
}

.view-more:hover {
  transform: translateY(-2px);
  box-shadow: 0rem 0.4rem 0.2rem 0rem rgba(0,0,0,.15);
}

