body {
  font-family: 'Abril Fatface' !important;
  color: #fff;
  background-color: #fff;
  min-height: 100vh;
  letter-spacing: 1px;
}

.studio-background {
  background-image: url("../images/home/studio.jpg");
  background-size: cover;
}

.attraction-header {
  background-color: #eabf00;
  box-shadow: 0px 0px 5px 0px rgb(51, 34, 18);
  text-align: center;
  color: #fff;
  padding: 5px;
}


.listen-now {
  background-color: #eabf00;
  width: 30%;
  min-width: 300px;
  margin: auto;
  border-radius: 2%;
  box-shadow: 0px 0px 5px 0px #fff;
}

.youtube-video {
  box-shadow: 0px 0px 5px 0px #fff;
  width: 100%;
  height: auto;
  min-height: 315px;
  min-width: 290px;
}

a,
a:focus,
a:hover {
  color: #fff;
}

.listen-item {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0px 0px 5px 0px #fff;
  height: 4rem;
}

.listen-item:hover {
  box-shadow: 0px 0px 10px 0px #eabf00;
}

.team-icons:hover {
  box-shadow: 0px 0px 10px 0px #eabf00;
  border: 2px solid #fff;
  border-radius: 50%;
}

.podcast-icons:hover {
  box-shadow: 0px 0px 10px 0px #eabf00;
  border: 2px solid #fff;
  border-radius: 25%;
}

.list-inline{
  height: 2rem;
}


.botmw-text-design-beer {
  position: relative;
  bottom: 250px;
  transform: rotate(-15deg);
  font-size: 2rem;
  background-color: #eabf00;
  width: 100%;
  padding: 5px;
  margin: auto;
  color: #fff;
  border-radius: 2%;
  box-shadow: 0px 0px 5px 0px rgb(51, 34, 18);
  max-width: 460px;
}

#chevron-attraction {
  position: relative;
  text-align: center;
  padding: 12px;
  margin-bottom: 6px;
  height: 70px;
  width: 100%;
  top: 40px;
}
#chevron-attraction:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 130%;
  width: 50%;
  background: #fff;
  transform: skew(0deg, 6deg);
}
#chevron-attraction:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 130%;
  width: 50%;
  background: #fff;
  transform: skew(0deg, -6deg);
}


#chevron {
  position: relative;
  text-align: center;
  padding: 12px;
  margin-bottom: 6px;
  height: 50px;
  width: 225px;
  margin: auto;

}
#chevron:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 50%;
  width: 50%;
  background: #eabf00;
  transform: skew(0deg, 6deg);
  box-shadow: 0px 0px 5px 0px rgb(51, 34, 18);
  
}
#chevron:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 50%;
  width: 50%;
  background: #eabf00;
  transform: skew(0deg, -6deg);
  box-shadow: 0px 0px 5px 0px rgb(51, 34, 18);
}


.btn-brown {
  background-color: #eabf00;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid #eabf00;
  padding: .375rem .75rem;
  font-size: 1.5rem;
  line-height: 1.5;
  border-radius: .25rem;
  box-shadow: 0rem 0rem 1rem 0.5rem #eee;
}

.btn-brown:hover {
  box-shadow: 0px 0px 5px 0px rgb(51, 34, 18);
}

.row{
  max-width: 1200px;
}

.mt-6 {
  margin-top: 6rem !important;
}

.mb-6 {
  margin-bottom: 6rem !important;
}

.pb-6 {
  padding-bottom: 6rem !important;
}

.pt-6 {
  padding-top: 6rem !important;
}

.header-title {
  font-size: 4.5rem;
}

/* Custom default button */
.btn-secondary,
.btn-secondary:hover,
.btn-secondary:focus {
  color: #fff;
  text-shadow: none;
  background-color: rgb(51, 34, 18,.9);
  border: .05rem solid #fff;
}

/*
 * Header
 */
.masthead {
  margin-bottom: 2rem;
}

.masthead-brand {
  margin-bottom: 0;
}

.nav-masthead .nav-link {
  padding: .25rem 0;
  font-weight: 700;
  color: rgba(255, 255, 255, .5);
  background-color: transparent;
  border-bottom: .25rem solid transparent;
}

.nav-masthead .nav-link:hover,
.nav-masthead .nav-link:focus {
  border-bottom-color: rgba(255, 255, 255, .25);
}

.nav-masthead .nav-link + .nav-link {
  margin-left: 1rem;
}

.nav-masthead .active {
  color: #fff;
  border-bottom-color: #fff;
}

@media (min-width: 48em) {
  .masthead-brand {
    float: left;
  }
  .nav-masthead {
    float: right;
  }
}


/*
 * Cover
 */
.cover {
  padding: 0 1.5rem;
}
.cover .btn-lg {
  padding: .75rem 1.25rem;
  font-weight: 700;
}


/*
 * Footer
 */

 @media screen and (max-width: 500px) {
   .bb-item-container {
     justify-content: space-around !important;
   }

  .safari_only {
      padding-bottom: 65px;
      font-size: small;
  }

  .modal-content .col-6 {
    padding: 0;
  }

  .media-body .text-muted {
    font-size: smaller;
  }

  .modal-body {
    padding: 1rem 0rem 1rem 0rem;
  }

  .media {
    padding: 1rem 0rem 1rem 0rem;
  }

  .modal-content .col {
    padding: 0;
  }

  .contact-name .col {
    padding-right: 5px !important;
    padding-left: 5px !important;
  }
}

.mastfoot {
  color: rgba(255, 255, 255, .5);
}

@media only screen and (max-device-width: 1366px) {
  body {
    background-size: auto 100vh;
  }


  .home-title {
    font-size: 3.5rem;
  }

  .sub-title {
    font-size: 1.2rem;
  }
  

}

@media only screen and (max-device-width: 700px) {

  .home-title {
    font-size: 2.5rem;
  }

  .sub-title {
    font-size: 1rem;
  }

  .team-christian {
    right: 10%;
  }

  .team-member {
    float: initial;
  }

  .slide-container {
    margin: auto;
  }

  .team-col, .team-header-content {
    min-width: 100% !important;
    margin-bottom: 2rem;
  }
  

}

@media only screen and (max-device-width: 1100px) {
  .contact-section {
    background-image: linear-gradient(105deg, rgba(51, 34, 18, 0.5) 0%, rgba(51, 34, 18, 0.9) 100%, transparent 50%), url('/media/contact-background.jpg');
  background-size: 100%;
  height: 400px;
  background-size: auto;
  }

  .contact-section .d-block {
    margin: auto !important;
  }

  .contact-section form {
    width: 90%;
    margin: auto;
  }

}

@media only screen and (max-device-width: 410px) {
  .footer-content {
    width: 50%;
  }

  .team-christian {
    right: 5%;
  }

}

